import React, { Component } from 'react';
import { Form, Button, Col , Row, Image} from 'react-bootstrap';
import lady from '../Images/chicalaptop.jpg';
import Swing from 'react-reveal/Swing';
import Zoom from 'react-reveal/Zoom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import ReactGA from 'react-ga';

export default class extends Component {
    constructor(props) {
        super(props);
        this.state = { 
        feedback: '', 
        name: '', 
        email: '',
        phone: '',
    };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        
    }
    componentWillMount(){
        ReactGA.initialize('UA-165927060-1');
    }
    componentDidMount(){
        ReactGA.pageview("/contact");
    }
    
    render() {
        return (<>
            <Row style={{marginBottom:10,marginTop:10}}>
                <Col sm={12} md={6}>
                <Zoom>
                <Form noValidate id="formContact" onSubmit={this.handleSubmit} validated={this.state.validated}>
                <h1>Puedes escribirme</h1>
                <Form.Group controlId="form.nombres">
                    <Form.Label>Nombre*</Form.Label>
                    <Form.Control type="text" id="name" name="name" placeholder="Tu nombre" 
                    value={this.state.name} onChange={this.handleChange} maxLength={50} required/>
                    <Form.Control.Feedback type="invalid">Por favor mencione su nombre</Form.Control.Feedback>
                </Form.Group>
                <Form.Group controlId="exampleForm.ControlInput1">
                    <Form.Label>Correo*</Form.Label>
                    <Form.Control type="email" id="email" name="email" value={this.state.email} 
                    placeholder="name@example.com" onChange={this.handleChange} maxLength={50} required/>
                    <Form.Control.Feedback type="invalid">Por favor mencione su Correo</Form.Control.Feedback>
                </Form.Group>
                <Form.Group controlId="exampleForm.ControlSelect1">
                    <Form.Label>Teléfono</Form.Label>
                    <Form.Control type="text" id="phone" name="phone" placeholder="123456789" 
                    value={this.state.phone} onChange={this.handleChange} maxLength={16} />
                </Form.Group>
                <Form.Group controlId="exampleForm.ControlTextarea1">
                    <Form.Label>Mensaje*</Form.Label>                    
                    <Form.Control as="textarea" rows="3" 
                        maxLength={500}
                        id="test-mailing"
                        name="feedback"
                        onChange={this.handleChange}
                        placeholder="Escribe tu mensaje aquí"
                        required
                        value={this.state.feedback}
                        style={{ width: '100%', height: '150px' }}
                    />
                    <Form.Control.Feedback type="invalid">Por favor escriba su mensaje</Form.Control.Feedback>
                </Form.Group>
                <Button type="submit" variant="primary">Enviar</Button>                
                </Form>
                </Zoom>
                </Col>
                <Col sm={12} md={6}>
                    <Swing>
                    <Image src={lady} width={"100%"} height={"auto"} rounded/>
                    </Swing>
                    
                </Col>
            </Row>
            <ToastContainer />
            </>
        )
    }

    handleChange(e) {
        this.setState({ [e.target.name]: e.target.value })
    }
    successMessage(menssage){
        toast.success(menssage);
    }
    errorMessage(menssage){
        toast.error(menssage);
    }
    cleanForm(){
        this.setState({
            feedback: '', 
            name: '', 
            email: '',
            phone: '',
        })
    }
    handleSubmit(e) {
        this.setState({ isLoading: true })
        e.preventDefault();
        const form = e.currentTarget;
        if (form.checkValidity() === false) {
            e.preventDefault();
            e.stopPropagation();
            this.setState({
                validated: true
            })
        } else {
      
            let os=navigator.oscpu;
            let navi=navigator.platform;
            let agent=navigator.userAgent;
            let version=navigator.vendorSub;
        
        const templateId = 'template_GaJxcYNc';
        this.sendFeedback(templateId, {
            message_html: this.state.feedback, 
            from_name: this.state.name,
            to_name:"Eliezer",
            reply_to: this.state.email,
            phone:this.state.phone,
            user_os:os,
            user_platform:navi,
            user_browser:agent,
            user_version:version
        });
        }
    }
    sendFeedback(templateId, variables) {
        window.emailjs.send(
            'gmailMyPage', templateId,
            variables
        ).then(res => {
            this.cleanForm();
            this.successMessage("Enviado correctamente");
        })
        .catch(
            err => {
                this.cleanForm();
                console.error('Oh well, you failed. Here some thoughts on the error that occured:', err);
                this.errorMessage("Ha ocurrido un error: "+err);
            });
    }
}