import React, { Component } from 'react';
import styles from './index.module.css';
import { Row, Col, Card, Button, Jumbotron, Container, Alert, Image } from 'react-bootstrap';
import java from '../Images/java.png'
import reactImg from '../Images/reactjs.png'
import androide from '../Images/androide.jpg'
import covid2 from '../Images/covid2.png'
import ImagePrincipal from '../Images/desingweb2.jpg'
import { Parallax } from "react-parallax";
import Zoom from 'react-reveal/Zoom';
import ReactGA from 'react-ga';
import {Link} from "react-router-dom";

let usesInternStyle = {
    padding: 20,
    position: "absolute",
    top: "50%",
    left: "85%",
    transform: "translate(-85%,-50%)",
    color: "#fff",
    fontSize: "20px",
    width:"100%"
}

class Index extends Component {
    constructor(props){
        super(props);
        this.state={
            isLoading:false
        }
        
    }
    componentWillMount(){
        ReactGA.initialize('UA-165927060-1');
    }
    componentDidMount(){
        ReactGA.pageview("/");
    }
    onClickToReference(e){
        //alert(e.target.name);
        ReactGA.event({
            category: 'Evento en la pagina Index',
            action: 'Clic en '+e.target.name,
            label: 'Covid-19 Tracker'
        });
    }

    render() {
        return (
            <>
                <hr />
                <Row>
                    <Col>
                        <Parallax bgImage={ImagePrincipal} strength={500}>
                            <div style={{ height: 500 }}>
                                <div style={usesInternStyle}>
                                    <h2 style={{color:'white'}}>Tu mundo en Java!</h2>
                                    <p>Lleva tu empresa al siguiente nivel con nuestra tecnología basada en Java, desarrollo
                                        de software a la medida, contratos como outsourcing, consultoría relacionada con Java
                                        y mucho más.
                                </p>                                   
                                </div>
                            </div>
                        </Parallax>
                    </Col>
                </Row>


                <Row style={{marginTop:10,marginBottom:10}}>
                    <Col sm={12} md={4}>
                        <Card style={{ width: '100%' }}>
                            <Card.Img variant="top" src={java} />
                            <Card.Body>
                                <Card.Title>Software en Java</Card.Title>
                                <Card.Text>
                                    Se desarrolla software a la medida con la tecnología más
                                    actual en Java.
                            </Card.Text>
                                <Link to="javainfo" className="btn btn-primary"
                                onClick={this.onClickToReference.bind(this)}
                                name="button_to_java_info">Ver mas <i class="fas fa-chevron-right"></i></Link>                                
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col sm={12} md={4}>
                        <Card style={{ width: '100%' }}>
                            <Card.Img variant="top" src={reactImg} />
                            <Card.Body>
                                <Card.Title>Aplicaciones y páginas web con ReactJS</Card.Title>
                                <Card.Text>
                                    Creación de SPA (Single page aplications) y páginas web.
                            </Card.Text>
                            <Link to="reactinfo" className="btn btn-primary"
                                onClick={this.onClickToReference.bind(this)}
                                name="button_to_react_info">Ver mas <i class="fas fa-chevron-right"></i></Link>                                
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col sm={12} md={4}>
                        <Card style={{ width: '100%' }}>
                            <Card.Img variant="top" src={androide} />
                            <Card.Body>
                                <Card.Title>Desarrollo móvil en Android con Java</Card.Title>
                                <Card.Text>
                                    Apliaciones con las mejores prácticas de programación y con diseños
                                    muy amigables.
                            </Card.Text>
                                <Button variant="primary" onClick={this.onClickToReference.bind(this)}
                                name="button_to_android_info">Ver más <i class="fas fa-chevron-right"></i></Button>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
                <Zoom>
                <Jumbotron fluid className={styles.cardsbannerone}>
                    <Container>
                        <div className={styles.content}>
                            <Row>
                                <Col sm={12} md={6}>
                                    <Alert variant="success">
                                        <Alert.Heading>Covid-19 Tracker</Alert.Heading>
                                        <p>Dale un vistazo a nuestro trabajo con esta app desarrollada en ReactJS!</p>
                                        <Button variant="success" onClick={this.onClickToReference.bind(this)}
                                        id="button_covid_tracker" name="button_covid_tracker"
                                        href="https://elioelblack.github.io/covid19-tracker/">
                                            Ver más <i class="fas fa-chevron-right"></i></Button>
                                    </Alert>
                                </Col>
                                <Col sm={12} md={6}>
                                    <Image src={covid2} rounded />
                                </Col>
                            </Row>
                        </div>
                    </Container>
                </Jumbotron>
                </Zoom>
                
            </>
        )
    }
}
export default Index;